.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}
.nav-items > ul{
    list-style-type: none;
    display: flex;


}
.nav-items > ul > li{
    padding: 10px;
    
}
.logo{
    width: 100px;
}

.card{
    width: 300px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
}   
.card > img{
    width: 100%;
    height: 60%;
     
}
.restaurant-list    {
    display: flex;
    flex-wrap: wrap;
}
.shimmer-card {
    width: 300px;
    height: 400px;
    background: lightgray;
    margin: 20px;
  
}
.menu{
    display: flex;
}